import React, { useState } from 'react';
import { Grid, TextField, Typography } from "@mui/material";
import Button from '@mui/material/Button';
import { ReportTable } from "./ReportTable";

function App() {
  const [shipment, setShipment] = useState([]);
  const [inputValue, setInputValue] = useState("");

  const fetchDataByID = async (shipperID) => {
    fetch('/api/getShipment/' + shipperID)
      .then(result => result.json())
    .then(body => setShipment(body));
  };

  return (
    <div style={{ width: "100vw", height: "100vh" }}>
      <Grid container style={{ padding: "2rem" }}>
        <Grid>
          <Typography variant="h4" gutterBottom>
            Report
          </Typography>
        </Grid>
        <Grid>
          <Typography variant="h5" gutterBottom>
            Shipper ID:
          </Typography>
          <TextField
            label="Input field"
            value={inputValue}
            onChange={(event) => setInputValue(event.target.value)}
          />
          <Button onClick={() => fetchDataByID(inputValue)}>SUBMIT</Button>
        </Grid>
        <Grid>
          <Typography variant="h5" gutterBottom>
            Shipping Boxes Received Items:
          </Typography>
          <div>
            <ReportTable shipments={shipment} />
          </div>
        </Grid>
      </Grid>
    </div>
  );
}

export default App;
